function sliderMain() {
    var swiper = new Swiper('.main .mySwiper', {
        slidesPerView: 1,
        spaceBetween: 15,
        effect: "fade",
        navigation: {
            nextEl: ".main .swiper-button-next",
            prevEl: ".main .swiper-button-prev",
        },
        pagination: {
            el: ".main .swiper-pagination",
            clickable: true
        },
    })
}

function sliderDirectionsTabsHead() {
    var swiper = new Swiper('.directions__tabs-head .mySwiper', {
        slidesPerView: 'auto',
        spaceBetween: 35,
        slideToClickedSlide: 'true',
        breakpoints: {
            320: {
                spaceBetween: 15,
            },
            993: {
                spaceBetween: 35,
            }
        }
    })
}

let SlidersArray = []
function sliderDirectionsTabsBody() {
    $(".directions__content").each(function(index, el) {
        $(el).attr("data-slider-id", `${index}`)
        SlidersArray[index] = new Swiper(`.directions__content[data-slider-id="${index}"] .swiper`, {
            slidesPerView: 1,
            spaceBetween: 15,
            loop: true,
            effect: "fade",
            navigation: {
                nextEl: `.directions__content[data-slider-id="${index}"] .swiper-button-next`,
                prevEl: `.directions__content[data-slider-id="${index}"] .swiper-button-prev`,
            },
            pagination: {
                el: `.directions__content[data-slider-id="${index}"] .swiper-pagination`,
                clickable: true
            },
        })
    })
}

