$(document).ready(function () {
    sliderMain();
    sliderDirectionsTabsHead();
    sliderDirectionsTabsBody();
    sliderProducts();
    sliderCategories();
    sliderPortfolio();
    sliderBreadcrumbs();
    gallery();
    sliderReviews();
    sliderChapter();
    sliderCatalogMenu();
    sliderCategories2();
    sliderPortfolio2();


    // Search
    $('.search-btn').click(function () {
        $(this).parents('.search').addClass('search--active')
    })

    $('.search-input').on("keyup", function(e) {
        $(this).siblings('.search-title').removeClass('search-title--active');

        if($(this).val() !== "") {
            $(this).siblings('.search-title').addClass('search-title--active');
        }
    });

    // Menu
    $('.burger').click(function () {
        $(this).toggleClass('burger--active')
        $('.header-top').toggleClass('header-top--active')
        $('body').toggleClass('fixed-body')
    })

    // hover slider
    $(".hover__pag-item").mouseover(function() {
        let path = $(this).attr("data-tab-path")
        $(this).parents(".hover__slide").find(".hover__pag-item").removeClass("hover__pag-item--active")
        $(this).addClass("hover__pag-item--active")
        $(this).parents(".hover__slide").find(".hover__img").removeClass("hover__img--active")
        $(this).parents(`.hover__slide`).find(`.hover__img[data-tab-path="${path}"]`).addClass("hover__img--active")
    })

    // Tabs
    $(".tab").click(function () {
        let path = $(this).attr("data-tab-path")
        $(this).siblings(".tab").removeClass("tab--active")
        $(this).addClass("tab--active")

        $(this).parents(".tabs-inner").find(".tab__content").removeClass("tab__content--active")
        $(this).parents(".tabs-inner").find(`.tab__content[data-content-path="${path}"]`).addClass("tab__content--active")
    })

    // Accordion
    $(".item-show").click((function(){
        $(this).toggleClass("item-show--active"),
        $(this).siblings(".item-hidden").slideToggle()
    }));

    //open-more
    $('.open-more').click(function () {
        $(this).toggleClass('open-more--active')
        $(this).prev('.product-info__group').toggleClass('more-text')
        $(this).siblings('.catalog__link').toggleClass('more-link')
        $(this).siblings('.more-list').toggleClass('more-list--active')
    })

    // Select
    $(".custom_select select").niceSelect()

    // Sorting-cards
    $('.sorting-card--1').click(function () {
        $(this).addClass('active')
        $('.sorting-card--2').removeClass('active')
        $('.catalog__cards').addClass('sorting-cards--1')
        $('.catalog__cards').removeClass('sorting-cards--2')
    })

    $('.sorting-card--2').click(function () {
        $(this).addClass('active')
        $('.sorting-card--1').removeClass('active')
        $('.catalog__cards').removeClass('sorting-cards--1')
        $('.catalog__cards').addClass('sorting-cards--2')
    })

    // Filter
    $('.filter-btn').click(function () {
        $('.catalog-sidebar').addClass('catalog-sidebar--active')
        $('body').addClass('fixed-body')
    })

    $('.catalog-sidebar__close').click(function () {
        $('.catalog-sidebar').removeClass('catalog-sidebar--active')
        $('body').removeClass('fixed-body')
    })

    // Basket-btn
    $('.basket-btn').click(function () {
        $(this).toggleClass('basket-btn--active')
        $(this).parents('.basket-card').find('.number-products').addClass('number-products--active')
    })



    // Calc
    function cartCalc(){
        $('.cartcalc .ccalc-minus').click(function(){
            let a = $(this).closest('.cartcalc').find('input').val();
            if(a>1){
                let b = +a-1;
                $(this).closest('.cartcalc').find('input').val(b);
            }else{
                $(this).closest('.cartcalc').find('input').val(a);
            }
        });
        $('.cartcalc .ccalc-plus').click(function(){
            let a = $(this).closest('.cartcalc').find('input').val();
            let b = +a+1;
            $(this).closest('.cartcalc').find('input').val(b);
        });
    }
    cartCalc()

    // Basket-card
    $('.basket-card__show').click(function () {
        $(this).parents('.basket-card').toggleClass('basket-card--active')
    })

    $('.basket-card__delete').click(function () {
        $(this).parents('.basket-card').find('.delete-card').addClass('delete-card--active')
    })

    $('.recover-btn').click(function () {
        $(this).parents('.delete-card').removeClass('delete-card--active')
    })



    // Price - Range
    const rangeSlider = document.getElementById('range-slider');

    if (rangeSlider) {
        noUiSlider.create(rangeSlider, {
            start: [0, 100],
            connect: true,
            step: 1,
            range: {
                'min': [0],
                'max': [100]
            }
        });

        const input0 = document.getElementById('input-0');
        const input1 = document.getElementById('input-1');
        const inputs = [input0, input1];

        rangeSlider.noUiSlider.on('update', function(values, handle){
            inputs[handle].value = Math.round(values[handle]);
        });

        const setRangeSlider = (i, value) => {
            let arr = [null, null];
            arr[i] = value;

            console.log(arr);

            rangeSlider.noUiSlider.set(arr);
        };

        inputs.forEach((el, index) => {
            el.addEventListener('change', (e) => {
                console.log(index);
                setRangeSlider(index, e.currentTarget.value);
            });
        });
    }


})